'use client';
import { useEffect, useState } from 'react';
import { createClient } from '@supabase/supabase-js';

const supabase = createClient(
  process.env.NEXT_PUBLIC_SUPABASE_URL,
  process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY
);

export default function WebsitesPage() {
  const [websites, setWebsites] = useState([]);
  const [filtered, setFiltered] = useState([]);
  const [filters, setFilters] = useState({
    search: '',
    category: '',
    minDA: '',
    maxPrice: ''
  });

  useEffect(() => {
    const fetchWebsites = async () => {
      const { data, error } = await supabase
        .from('web_sites')
        .select('*')
        .order('created_at', { ascending: false });

      if (!error) {
        setWebsites(data);
        setFiltered(data);
      }
    };
    fetchWebsites();
  }, []);

  useEffect(() => {
    const { search, category, minDA, maxPrice } = filters;
    const results = websites.filter((site) => {
      const matchSearch =
        site.link?.toLowerCase().includes(search.toLowerCase()) ||
        site.category_1?.toLowerCase().includes(search.toLowerCase());
      const matchCategory =
        !category ||
        [site.category_1, site.category_2, site.category_3]
          .map((c) => c?.toLowerCase())
          .includes(category.toLowerCase());
      const matchDA = !minDA || (site.moz_da || 0) >= parseInt(minDA);
      const matchPrice = !maxPrice || (site.price_to || 0) <= parseInt(maxPrice);

      return matchSearch && matchCategory && matchDA && matchPrice;
    });
    setFiltered(results);
  }, [filters, websites]);

  return (
    <div className="max-w-7xl mx-auto px-4 py-6">
      <h1 className="text-2xl font-bold mb-4">Guest Posting Websites</h1>

      {/* Filters */}
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-6">
        <input
          className="p-2 border rounded"
          type="text"
          placeholder="Search by link or niche"
          value={filters.search}
          onChange={(e) => setFilters({ ...filters, search: e.target.value })}
        />
        <input
          className="p-2 border rounded"
          type="text"
          placeholder="Category (e.g., Tech)"
          value={filters.category}
          onChange={(e) => setFilters({ ...filters, category: e.target.value })}
        />
        <input
          className="p-2 border rounded"
          type="number"
          placeholder="Min DA"
          value={filters.minDA}
          onChange={(e) => setFilters({ ...filters, minDA: e.target.value })}
        />
        <input
          className="p-2 border rounded"
          type="number"
          placeholder="Max Price"
          value={filters.maxPrice}
          onChange={(e) => setFilters({ ...filters, maxPrice: e.target.value })}
        />
      </div>

      {/* Data Table */}
      <div className="overflow-x-auto rounded border">
        <table className="min-w-full text-sm border">
          <thead className="bg-gray-100 text-left">
            <tr>
              <th className="px-4 py-2">Link</th>
              <th className="px-4 py-2">Categories</th>
              <th className="px-4 py-2">Price</th>
              <th className="px-4 py-2">Traffic</th>
              <th className="px-4 py-2">DA / AS / DR</th>
              <th className="px-4 py-2">TAT</th>
              <th className="px-4 py-2">Link Type</th>
            </tr>
          </thead>
          <tbody>
            {filtered.map((site) => (
              <tr key={site.id} className="border-t hover:bg-gray-50">
                <td className="px-4 py-2 text-blue-600 underline">
                  <a href={site.link} target="_blank">{site.link}</a>
                </td>
                <td className="px-4 py-2">
                  {[site.category_1, site.category_2, site.category_3].filter(Boolean).join(', ')}
                </td>
                <td className="px-4 py-2">
                  ${site.price_from} - ${site.price_to}
                </td>
                <td className="px-4 py-2">{site.similarweb_traffic}</td>
                <td className="px-4 py-2">
                  DA: {site.moz_da} <br />
                  AS: {site.semrush_as} <br />
                  DR: {site.ahrefs_dr_range}
                </td>
                <td className="px-4 py-2">{site.tat}</td>
                <td className="px-4 py-2">{site.link_attribution_type}</td>
              </tr>
            ))}
          </tbody>
        </table>
        {filtered.length === 0 && (
          <p className="p-4 text-center text-gray-500">No results match your filters.</p>
        )}
      </div>
    </div>
  );
}
